.piece {
  display: flex;
  position: absolute;
  margin: auto;
  width: var(--tileSize);
  height: var(--tileSize);
  border-radius: 3px;
  z-index: 2;
}
.piece-state-0 {
  z-index: 2;
}
.piece-state-1 {
  animation: pop 0.1s;
}
.piece-state-2 {
  animation: new 0.1s;
}

@keyframes new {
  0% {
    scale: 0;
  }
  100% {
    scale: 1;
  }
}

@keyframes pop {
  0% {
    scale: 1;
  }
  50% {
    scale: 1.1;
  }
  100% {
    scale: 1;
  }
}

.loc-0 {
  left: calc(var(--spaceSize) + calc(var(--translateunit) * 0));
  top: calc(var(--spaceSize) + calc(var(--translateunit) * 0));
}

