.header-container {
  margin-top: 5%;
  display: flex;
}

.title-container {
  display: flex;
  width: calc(var(--tileSize) * 1.2);
  height: calc(var(--tileSize) * 1.2);
  border-radius: 6px;
  background: #edc22e;
  padding: auto;
  margin: auto;
  margin-left: 0;
}
.title {
  color: #f9f6f2;
  font-weight: bold;
  margin: auto;
}

.score-container {
  display: flex;
  flex-direction: row;
  margin-bottom: auto;
  margin-left: auto;
  width: 50%;
}
.score {
  display: flex;
  flex-direction: column;
  background: #bbada0;
  font-weight: bold;
  border-radius: 3px;
  color: white;
  margin: 2px;
  text-align: center;
  width: var(--tileSize);
}
.score-label {
  margin-top: 7%;
  color: rgb(238, 228, 218);
}

.instructions-text {
  margin-top: 2%;
  color: #776e65;
  font-weight: bold;
  padding: auto;
  text-align: center;
}
