.App {
  display: flex;
  flex-direction: column;
  --squareSize: 500px;
  --spaceSize: calc(var(--squareSize) * 0.03);
  --tileSize: calc(var(--squareSize) * 0.2125);
  --translateunit: calc(var(--spaceSize) + var(--tileSize));
  width: var(--squareSize);
  margin: auto;

  --smallNumberFontSize: 50px;
  --largeNumberFontSize: 45px;
}
.title {
  font-size: 40px;
}

.score-number {
  font-size: 25px;
}

.score-label {
  font-size: 12px;
}
.piece-number {
  margin: auto;
  font-weight: bold;
}

@media (max-width: 520px) {
  .App {
    --squareSize: 300px;
    --spaceSize: calc(var(--squareSize) * 0.03);
    --tileSize: calc(var(--squareSize) * 0.2125);
    --translateunit: calc(var(--spaceSize) + var(--tileSize));
    --smallNumberFontSize: 32px;
    --largeNumberFontSize: 26px;
  }
  .header-container {
    margin-top: 30%;
  }
  .title {
    font-size: 23px;
  }

  .score-number {
    font-size: 23px;
  }

  .score-label {
    font-size: 10px;
  }
  .piece-number {
    font-weight: bold;
  }
  body {
    font-size: 12px;
  }
}
